body{
  background-color: #2B283A;
}
.App {
  text-align: center;
  font-family: inter;
  position: absolute;
  width: 320px;
  left: 0; 
  right: 0; 
  margin-left: auto; 
  margin-right: auto; 
  background-color: white;
}
p{
  font-size: 12px;
}
navbar{
  position: absolute;
  top: 20px;
  left: 0; 
  right: 0;
  background-color: white;
}
#profilePic{
  width: 320px;
  gap: 0px;
}
navbar h3{
  font-size: 16px;
  color: #F47D27;
}
navbar p{
  color: blue;
  cursor: pointer;
}
.buttons{
  display: block;
  width: 280px;
  left: 0; 
  right: 0; 
  margin-left: auto; 
  margin-right: auto; 
  margin-top: -5px;
}
navbar button{
  width: 130px;
  height: 40px;
  font-family: inter;
  font-weight: 600;
  font-size: 15px;
  border: 1px solid #D1D5DB;
  border-radius: 6px;
  cursor: pointer;
}
.btn1{
  float: left;
}
.icon{
  width: 14px;
  height: 13px;
  padding-right: 10px;
}
.linkedinIcon{
  width: 16px;
  height: 13px;
}
.btn2{
  float: right;
  background-color: #297FE5;
  color: white;
}
main{
  position: absolute;
  width: 240px;
  height: 350px;
  top: 524px;
  background-color: white;
  padding-left: 40px;
  padding-right: 40px;
}
main h2{
  text-align: left;
  font-weight: 700px;
  color: #2B283A;
}
main p{
  line-height: 20px;
  text-align: left;
  margin-top: -12px;
  color: #4A4E74;
}
footer{
  position: absolute;
  width: 320px;
  height: 60px;
  top: 850px;
  left: 0; 
  right: 0; 
  margin-left: auto; 
  margin-right: auto; 
  background-color: #D5D4D8;
}
footer img{
  position: absolute;
  top: 25%;
  width: 25px;
  height: 25px;
  cursor: pointer;
}
footer .twitterIcon{
  left: 15%;
}
footer .faceIcon{
  left: 35%;
}
footer .instaIcon{
  left: 55%;
}
footer .gitIcon{
  left: 75%;
}